<template>
	<view class="publish">
		<view class="">{{is_empower}}</view>
		<view class="publish_item align-c">
			<button v-if="is_empower == 1" open-type="getUserInfo" @getuserinfo="getWxUser" class="btn_empower"></button>
			<view @click='navToPage(1)'>
				<image src="../../static/img/bg_pinche.png"></image>
				<view class="f-40 color-fff top_135">
					<text>寄物搭车</text>
				</view>
				<view class="f-30 color-fff eng">carpool</view>
			</view>
		</view>
		<view class="publish_item align-c">
			<button v-if="is_empower == 1" open-type="getUserInfo" @getuserinfo="getWxUser" class="btn_empower"></button>
			<view @click='navToPage(2)'>
				<image src="../../static/img/bg_carry.png"></image>
				<view class="f-40 color-fff top_135">
					<text>带人带物</text>
				</view>
				<view class="f-30 color-fff eng">commerce</view>
			</view>

		</view>
	</view>
</template>

<script>
	import MyApi from '../../apis/my.js'
	export default {
		data() {
			return {
				is_empower: 1
			}
		},
		mounted() {
			this.is_empower = uni.getStorageSync('isEmpower')
		},
		methods: {
			navToPage(type) {
				uni.navigateTo({
					url: '/pages/publish/form?type=' + type
				})
			},
			getWxUser(e) {
				if (e.detail.errMsg == 'getUserInfo:ok') {
					let data = {
						nickname: e.detail.userInfo.nickName,
						avatar: e.detail.userInfo.avatarUrl,
					}
					this.user = Object.assign({}, this.user, data)
					this.setInfo(data)
				}
			},
			setInfo(data) {
				MyApi.userSet(data).then(res => {
					this.is_empower = 2
					uni.setStorageSync('isEmpower', 2)
					uni.showToast({
						title: '授权成功',
					})

				})
			},
		},
		onShareAppMessage() {
			return {
				title: '发布',
				path: '/pages/publish/index', // 自定义的分享路径，点击分享的卡片之后会跳转这里定义的路由
				imageUrl: '', // 图片路径
			};
		}
	}
</script>

<style lang="less" scoped>
	.btn_empower {
		position: absolute;
		left: 0;
		top: 0;
		z-index: 9;
		width: 630rpx;
		height: 350rpx;
		background: transparent;
	}

	.publish_item {
		width: 630rpx;
		height: 350rpx;
		position: relative;
		margin: 60rpx auto;

		image {
			width: 630rpx;
			height: 350rpx;
			position: absolute;
			left: 0;
			right: 0;
			z-index: -2;
		}

		.top_135 {
			padding-top: 130rpx;
		}

		.eng {
			margin-top: 4rpx;
		}


	}
</style>
